html {
    background: #FAF7F2;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    font-weight: 400;
  }
  
  *, *:before, *:after {
    box-sizing: inherit;
  }
  
  .u-clearfix:before,
  .u-clearfix:after {
    content: " ";
    display: table;
  }
  
  .u-clearfix:after {
    clear: both;
  }
  
  .u-clearfix {
    *zoom: 1;
  }

  .photo-container {
    width: 52%;
    padding-left: 70px;
  }

  .card-media {
    padding-left: 43px;
    float: right;
  }
  
  .sg-subtle {
    color: #aaa;
  }
  
  .sg-card-container {
    margin: 25px auto 60px;
    position: relative;
    width: 750px;
  }
  
  .sg-card {
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    padding: 30px;
    position: relative;
    box-shadow: 0 0 5px rgba(75, 75, 75, .07);
    z-index: 1;
  }
  
  .sg-card-body {
    display: inline-block;
    /* float: left; */
    width: 310px;
  }
  
  .sg-card-number {
    margin-top: 15px;
  }
  
  .sg-card-circle {
    border: 1px solid #aaa;
    border-radius: 50%;
    display: inline-block;
    line-height: 22px;
    font-size: 12px;
    height: 25px;
    text-align: center;
    width: 25px;
  }
  
  .sg-card-author {
    display: block;
    font-size: 12px;
    letter-spacing: .5px;
    margin: 15px 0 0;
    text-transform: uppercase;
  }
  
  .sg-card-title {
    font-family: 'Cormorant Garamond', serif;
    font-size: 60px;
    font-weight: 300;
    line-height: 60px;
    margin: 10px 0;
  }
  
  .sg-card-description {
    display: inline-block;
    font-weight: 300;
    line-height: 22px;
    margin: 10px 0;
  }
  
  .sg-card-read {
    cursor: pointer;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 6px;
    margin: 5px 0 20px;
    position: relative;
    text-align: right;
    text-transform: uppercase;
  }
  
  .sg-card-read:after {
    background-color: #b8bddd;
    content: "";
    display: block;
    height: 1px;
    position: absolute;
    top: 9px;
    width: 75%;
  }
  
  .sg-card-tag {
    float: right;
    margin: 5px 0 0;
  }
  
  .sg-card-media {
    float: right;
  }
  
  .sg-card-shadow {
    background-color: #fff;
    box-shadow: 0 2px 25px 2px rgba(0, 0, 0, 1), 0 2px 50px 2px rgba(0, 0, 0, 1), 0 0 100px 3px rgba(0, 0, 0, .25);
    height: 1px;
    margin: -1px auto 0;
    width: 80%;
    z-index: -1;
  }

  .navbar {
    box-shadow: 0 2px 25px 2px rgba(0, 0, 0, 0.5);
    background: white;
  }

  .stafftable-item {
    width: 40%;
    margin: auto;
    -webkit-flex-grow: 1;
            flex-grow: 1;
