	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
}

.link-textupline a:hover,
.link-textupline a:focus {
	-webkit-transform: translateY(10%);
	transform: translateY(10%);
}

.link-textupline a::before {
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	background: #d1d1d1;
	content: '';
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	-webkit-transform: translateY(100%) translateY(-5px);
	transform: translateY(100%) translateY(-5px);
}

.link-textupline a,
.link-textupline a::before {
	-webkit-transition-timing-function: cubic-bezier(0.6,0,0.4,1);
	transition-timing-function: cubic-bezier(0.6,0,0.4,1);
}

.link-textupline a:hover::before,
.link-textupline a:focus::before {
	-webkit-transform: translateY(-100%) translateY(5px);
	transform: translateY(-100%) translateY(5px);
} 

/* Tooltip overlay */
.link-tipoverlay {
	background: #404d5b;
	color: #95a5a6;
}

.link-tipoverlay a {
	color: #fff;
}

.link-tipoverlay a::before {
	position: absolute;
	top: -50%;
	left: -50%;
	padding: 1.8em 1em 1em 3.7em;
	width: 200%;
	height: 200%;
	/* background: #fff url(../img/link.svg) no-repeat -3em 50%; */
	background-size: auto 40%;
	color: #404d5b;
	color: transparent;
	content: attr(href);
	white-space: pre-wrap;
	word-break: break-all;
	font-weight: 600;
	font-size: 50%;
	line-height: 1.2;
	opacity: 0.2;
	-webkit-transition: opacity 0.3s, color 0.3s, -webkit-transform 0.3s, background-position 0.2s;
	-webkit-transition: opacity 0.3s, color 0.3s, background-position 0.2s, -webkit-transform 0.3s;
	transition: opacity 0.3s, color 0.3s, background-position 0.2s, -webkit-transform 0.3s;
	transition: opacity 0.3s, color 0.3s, transform 0.3s, background-position 0.2s;
	transition: opacity 0.3s, color 0.3s, transform 0.3s, background-position 0.2s, -webkit-transform 0.3s;
	-webkit-transform: scale(0.5);
	transform: scale(0.5);
	pointer-events: auto;
}	

.link-tipoverlay a:hover::before,
.link-tipoverlay a:focus::before {
	background-position: 1em 50%;
	color: #404d5b;
	opacity: 1;
	-webkit-transition-delay: 0s, 0s, 0s, 0.1s;
	transition-delay: 0s, 0s, 0s, 0.1s;
	-webkit-transform: scale(1) translateY(-75%);
	transform: scale(1) translateY(-75%);
}

/* Arrow */
.link-arrow {
	background: rgba(149,165,166,0.4);
}

.link-arrow a {
	padding: 0 0.25em;
	font-weight: 500;
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	-webkit-transform: translateX(0);
	transform: translateX(0);
}

.link-arrow a:hover,
.link-arrow a::after {
	-webkit-transform: translateX(-1em);
	transform: translateX(-1em);
}

.link-arrow a::before,
.link-arrow a::after {
	position: absolute;
	height: 100%;
	content: '';
}

.link-arrow a::before {
	left: 0;
	z-index: -1;
	width: 100%;
	background: #fff;
}

.link-arrow a::after {
	left: 100%;
	z-index: -2;
	width: 1em;
	/* background: #34495e url(../img/arrow_right.svg) no-repeat 50% 50%; */
	background-size: 60% auto;
	text-align: center;
	font-family: Arial, sans-serif;
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	pointer-events: auto;
}

.link-arrow a:hover::after,
.link-arrow a:focus::after {
	-webkit-transform: translateX(0);
	transform: translateX(0);
}

/* Curtain */
.link-curtain {
	background: rgba(149,165,166,0.3);
}

.link-curtain a {
	overflow: hidden;
	font-weight: 500;
}

.link-curtain a::before,
.link-curtain a::after {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	content: '';
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
}

.link-curtain a::before {
	border-top: 2px solid #fff;
	background: transparent;
	-webkit-transition-delay: 0.3s;
	transition-delay: 0.3s;
	-webkit-transform: translateY( calc(100% - 2px) );
	transform: translateY( calc(100% - 2px) );
}

.link-curtain a:hover::before,
.link-curtain a:focus::before {
	-webkit-transition-delay: 0s;
	transition-delay: 0s;
}

.link-curtain a:hover::before,
.link-curtain a:focus::before,
.link-curtain a:hover::after,
.link-curtain a:focus::after {
	-webkit-transform: translateX(0);
	transform: translateX(0);
}

.link-curtain a::after {
	z-index: -1;
	background: rgba(255,255,255,0.7);
	-webkit-transform: translateY(-100%);
	transform: translateY(-100%);
}

.link-curtain a:hover::after,
.link-curtain a:focus::after {
	-webkit-transition-delay: 0.3s;
	transition-delay: 0.3s;
}

/* Text scale */
.link-braces a {
	padding: 0 0.25em;
	font-weight: 500;
	-webkit-transition: color 0.2s;
	transition: color 0.2s;
}

.link-braces a:hover {
	color: #d04c3f;
}

.link-braces a::before,
.link-braces a::after {
	position: absolute;
	top: 0;
	font-weight: 100;
	font-size: 150%;
	line-height: 1;
	opacity: 0;
	-webkit-transition: opacity 0.2s, -webkit-transform 0.2s;
	transition: opacity 0.2s, -webkit-transform 0.2s;
	transition: opacity 0.2s, transform 0.2s;
	transition: opacity 0.2s, transform 0.2s, -webkit-transform 0.2s;
}

.link-braces a::before {
	left: -0.1em;
	content: '[';
	-webkit-transform: translateX(-100%);
	transform: translateX(-100%);
}

.link-braces a::after {
	right: -0.1em;
	content: ']';
	-webkit-transform: translateX(100%);
	transform: translateX(100%);
}

.link-braces a:hover::before,
.link-braces a:hover::after {
	opacity: 1;
	-webkit-transform: translateX(0);
	transform: translateX(0);
}

/* Text fall */
.link-textfall {
	background: #34495e;
	color: #fff;
}

.link-textfall a {
	color: #4c8fbb;
	font-weight: 400;
	-webkit-transition: color 0.3s;
	transition: color 0.3s;
	-webkit-perspective: 1000px;
	perspective: 1000px;
}

.link-textfall a:hover {
	color: #afd6f0;
}

.link-textfall a::before,
.link-textfall a::after {
	position: absolute;
	top: 0;
	left: 0;
	content: attr(data-dummy);
	opacity: 0;
	-webkit-transition: -webkit-transform 0.3s, opacity 0.3s, color 0.3s;
	-webkit-transition: opacity 0.3s, color 0.3s, -webkit-transform 0.3s;
	transition: opacity 0.3s, color 0.3s, -webkit-transform 0.3s;
	transition: transform 0.3s, opacity 0.3s, color 0.3s;
	transition: transform 0.3s, opacity 0.3s, color 0.3s, -webkit-transform 0.3s;
	pointer-events: none;
}

.link-textfall a::before {
	color: #7db8de;
	-webkit-transition-delay: 0.15s;
	transition-delay: 0.15s;
	-webkit-transform: translateY(-100%) translateZ(100px) rotateX(30deg) rotateZ(-10deg);
	transform: translateY(-100%) translateZ(100px) rotateX(30deg) rotateZ(-10deg);
}

.link-textfall a::after {
	color: #afd6f0;
	-webkit-transform: translateY(-100%) translateZ(100px) rotateX(-30deg) rotateZ(10deg);
	transform: translateY(-100%) translateZ(100px) rotateX(-30deg) rotateZ(10deg);
}

.link-textfall a:hover::before,
.link-textfall a:hover::after {
	color: transparent;
	opacity: 1;
	-webkit-transform: translateY(0) translateZ(0) rotateX(0) rotateZ(0);
	transform: translateY(0) translateZ(0) rotateX(0) rotateZ(0);
}

.link-textfall a:hover::before {
	-webkit-transition-delay: 0s;
	transition-delay: 0s;
}

.link-textfall a:hover::after {
	-webkit-transition-delay: 0.15s;
	transition-delay: 0.15s;
}

